import { Injectable } from '@angular/core';
import { IViewComponentDescriptor, IViewDefinitionPermission } from '@helix/platform/view/api';
import { IPlainObject } from '@helix/platform/shared/api';
import { isFunction } from 'lodash';

@Injectable({
  providedIn: 'root'
})
export class RxViewDesignerStateHelperService {
  getInitialComponentProperties(
    initialProperties: IPlainObject,
    componentDescriptor: IViewComponentDescriptor
  ): IPlainObject {
    let result = initialProperties;

    if (isFunction(componentDescriptor.designComponentModel?.getInitialProperties)) {
      result = componentDescriptor.designComponentModel.getInitialProperties(initialProperties);
    }

    return result;
  }

  getDefaultComponentPermissions(componentDescriptor: IViewComponentDescriptor): IViewDefinitionPermission[] {
    let permissions = null;

    if (isFunction(componentDescriptor.designComponentModel?.getDefaultPermissions)) {
      permissions = componentDescriptor.designComponentModel.getDefaultPermissions();
    }

    return permissions;
  }
}
